import { masks } from "@/plugins/phone_mask.js";
export default {
  data: () => ({
    asd: "",
    ddd: "",
    popup: false,
    formm: { test: "" },
    name_form: "",
    selected_mask: "#-##-##",
    default_mask: "Kazakhstan",
    phone_masks: [],
    phone_number: "",
    test: "",
    form: {},
    textarea: "",
    name: "",
    number: "",
    qwe: [
      "+7 ",
      "(",
      /\d/,
      /\d/,
      /\d/,
      ") ",
      /\d/,
      /\d/,
      /\d/,
      "-",
      /\d/,
      /\d/,
      "-",
      /\d/,
      /\d/,
    ],
    name_validate: [/[^a-zA-Z\-]/],
  }),
  created() {
    this.phone_masks = [];
    Object.keys(masks).forEach((key) => {
      this.phone_masks.push({ text: key, value: masks[key] });
    });
  },
  computed: {
    mobileRules() {
      return [(v) => !!v || this.$t("musthave")];
    },
    rules() {
      return [(value) => !!value || this.$t("musthave")];
    },
  },

  methods: {
    OutsideClick() {
      this.popup = false;
    },
    showPopup() {
      if (!this.$refs.form.validate()) return;
      else {
        this.popup = true;
        const postData = {
          full_name: this.name,
          phone: this.asd,
          message: this.textarea,
        };
        this.$axios
          .post("save-request", postData)
          .then(() => {
            this.$refs.form.reset();
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },
    closePopup() {
      this.popup = false;
      this.$refs.form.reset();
    },
  },
};
